@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap");
* {
	font-family: "Poppins", sans-serif;
  padding: 10px;
  margin: auto
}
.App{
  width: 100%;
}
.card {
	border-radius: 15px;
	background: linear-gradient(145deg, #f0f0f0, #cacaca);
	box-shadow: 18px 18px 37px #bebebe, -18px -18px 37px #ffffff;
  width: 60%;
  margin-bottom: 5px;
  padding: 10px;
}
nav{
  display: flex;
  justify-content: space-between;
  /* background-color: orange; */
}
ul li{
  display: flex;
  text-decoration: none;
  cursor: pointer;
}
ul {
  display: flex;
  
}
a{
  text-decoration: none;
  color: slategray;
}
p{
  color: slategray;
  font-weight: lighter;
}
h3{
  color: rgb(177, 38, 61);
  font: bold;
}
.NewBlogbtn{
  background-color: transparent;
  border: transparent;
  color: slategray;
  cursor: pointer;
}
form input{
  border: none;
  border-bottom: 1px solid slategray;
  margin: 4px;
  border-radius: 10px;
  background: transparent;
}
form button{
  background-color: rgb(177, 38, 61) ;
  color: white;
  width: 70px;
  border: none;
  border-radius: 8px;
  justify-content: center;
  cursor: pointer;
}
form{
  display: flex;
  flex-direction: column;
}
.deletebtn{
  border: none;
  color: white;
  border-radius: 10px;
  background-color:rgb(177, 38, 61) ;
}
.blog{
  display: flex;
}
@media (max-width:600px) {
  .card{
    width: 80%;
    background-color: orange;
  }
}